.form-box {
  &__row {
    display: flex;
    margin-left: -5px;
    margin-right: -5px;
  }
  &__head {
    color: $submain-color;
    font-weight: 600;
    margin-bottom: 10px;
  }
  &__item {
    width: 33.33%;
    padding: 5px;
  }
  &__item-row {
    padding-top: 5px;
    padding-bottom: 5px;
  }
  &__bottom {
    margin-top: 20px;
  }
  &__info {
    margin-top: 20px;
    margin-bottom: 20px;
    padding: 20px;
    border: solid 1px $submain-color;
    color: $main-color;
    text-align: center;
    font-size: 18px;
  }
}
